import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';


@IonicPage()
@Component({
  selector: 'page-cheques',
  templateUrl: 'cheques.html',
})
export class ChequesPage {
  langs: any = {};
  constructor(public navCtrl: NavController, public navParams: NavParams,public globalVars: GlobalVarsProvider) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() ); 
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ChequesPage');
  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }
  goHome() {
    this.navCtrl.setRoot( 'HomePage' );
  }
  openPage( mypage ) {
  
    this.navCtrl.push( 'ChequerequestPage', {
      title: mypage
    });

  }

}



